  width: 890px;
  background: rgba(0, 0, 0, .7);
  border-radius: .5em;
  animation: wievForm .3s;
  position: absolute;
  top: 54px;
  left: 4px;
}

@keyframes wievForm {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.form_toDo.hidden {
  display: none;
}

.form_toDo.close {
